import React, {ChangeEvent, useState} from 'react'
import {
    SOption,
    SSuperOption,
    SSuperOptionsList,
    SSuperSelectInput, SSuperSelectInputIcon,
    SSuperSelectInputWrapper,
    SSuperSelectWrapper
} from "./styled";
import ExpandMoreIcon from "../../../../icons/ExpandMoreIcon";

type TAlternativeSuperSelect = {
    options: Array<string>
    onChangeOption: (option: string) => void
    value: string
}

function AlternativeSuperSelect(props: TAlternativeSuperSelect) {

    const [opened, setOpened] = useState(false)
    const [hoveredElement, setHoveredElement] = useState(props.value)
    const onKeyHandler = (e: React.KeyboardEvent<HTMLDivElement>) => {
        e.preventDefault()
        for (let i = 0; i < props.options.length; i++) {
            if (e.key === 'ArrowDown') {
                if (props.options[i] === hoveredElement) {
                    props.onChangeOption(props.options[i + 1] ? props.options[i + 1] : props.options[0])
                    setHoveredElement(props.options[i + 1] ? props.options[i + 1] : props.options[0])
                }
            }
            if (e.key === 'ArrowUp') {
                if (props.options[i] === hoveredElement) {
                    props.onChangeOption(
                        props.options[i - 1] ? props.options[i - 1] : props.options[props.options.length - 1])
                    setHoveredElement(
                        props.options[i - 1] ? props.options[i - 1] : props.options[props.options.length - 1])
                }
            }
        }
    }

    const mappedOptions: any[] = props.options ? props.options.map((el, i) => (
        <SSuperOption
            onClick={() => onClickHandler(el)}
            onMouseEnter={() => setHoveredElement(el)}
            isHovered={hoveredElement === el}
            key={i}
        >
            {el}
        </SSuperOption>)
    ) : [] // map

    const onClickHandler = (el: any) => {
        props.onChangeOption(el)
        setOpened(false)
    }

    const wrapperRef: React.RefObject<HTMLDivElement> = React.useRef(null)

    return (
        <SSuperSelectWrapper ref={wrapperRef} onKeyUp={onKeyHandler} tabIndex={0} onBlur={() => setOpened(false)}
        >
            <SSuperSelectInputWrapper
                onClick={() => setOpened(!opened)}
            >
                <SSuperSelectInput>
                    {props.value}
                </SSuperSelectInput>
                <SSuperSelectInputIcon isOpen={opened}>
                    <ExpandMoreIcon />
                </SSuperSelectInputIcon>
            </SSuperSelectInputWrapper>
            {opened && (
                <SSuperOptionsList>
                    {mappedOptions || 'empty'}
                </SSuperOptionsList>
            )}
        </SSuperSelectWrapper>
    )
}

export default AlternativeSuperSelect
